import React, { Component } from 'react';
import { Breadcrumb, BreadcrumbItem, Card, CardTitle, CardImg } from 'reactstrap';
import {Link} from 'react-router-dom';




class Payroll extends Component   {
    
    constructor(props) {
        super(props);

        this.state = {
          depmenu: this.props.depmenu,
        }
    }



    render() {
    
        const RenderPayroll = ({depmenu}) => {

            return (
                <Card className="text-light bg-info border border-light m-5">
                    <CardTitle className="text-center pt-2">{depmenu.name}</CardTitle>
                    <CardImg src={depmenu.imgdep}/>
                </Card>
            );
        }



        const payroll = this.state.depmenu.map((depmenu) => {
            return (
                <div className="col-12 col-md-6 mb-2" key={depmenu.id}  >
                    <RenderPayroll depmenu={depmenu} />
                </div>
            );
        });

        return (
            <div className="container">
                <div className="row">
                    <Breadcrumb>
                        <BreadcrumbItem><Link to='/home'>Home</Link></BreadcrumbItem>
                        <BreadcrumbItem active>Thc n</BreadcrumbItem>
                    </Breadcrumb>
                    <div className="col-12 text-dark">
                        <h3>Thc n chi tit:</h3>
                        <hr />
                    </div>
                </div>
                <div className="row">
                    {payroll}
                </div>
            </div>
        );
    }
}


export default Payroll;


