import React, { Component } from "react";
import {
  Navbar,
  NavbarBrand,
  Nav,
  NavbarToggler,
  Collapse,
  NavItem,
  Jumbotron,
} from "reactstrap";
import { NavLink } from "react-router-dom";

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isNavOpen: false,
    };
    this.toggleNav = this.toggleNav.bind(this); // bind()  toggleNav c th gi c bn trong render
  }

  //responsive ca Navbar
  toggleNav() {
    this.setState({
      isNavOpen: !this.state.isNavOpen, //khi toggleNav c click, gi tr ca n lun lun khc isOpen
    });
  }
  render() {
    return (
      <React.Fragment>
        <Navbar dark expand="md" className="sticky-top">
          <div className="container">
            <NavbarToggler onClick={this.toggleNav} />
            <NavbarBrand className="mr-auto" href="/">
              <img
                src="assets/images/logo.png"
                height="50"
                width="130"
                alt="Chill..."
              />
            </NavbarBrand>
            <Collapse isOpen={this.state.isNavOpen} navbar>
              <Nav navbar>
                <NavItem>
                  <NavLink
                    onClick={this.toggleNav}
                    className="nav-link ml-5 mr-3 text-dark"
                    to="/home"
                  >
                    <span className="fa fa-home fa-lg"></span> Trang ch
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink
                    onClick={this.toggleNav}
                    className="nav-link ml-3 mr-3 text-dark"
                    to="/menu"
                  >
                    <span className="fa fa-users fa-lg"></span> Ni bt
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink
                    className="nav-link ml-3 mr-3 text-dark"
                    to="/departments"
                    onClick={this.toggleNav}
                  >
                    <span className="fa fa-cutlery fa-lg"></span> Danh mc
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink
                    className="nav-link ml-3 mr-3 text-dark"
                    to="/detailmenu"
                    onClick={this.toggleNav}
                  >
                    <span className="fa fa-coffee fa-lg"></span> Thc n
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink
                    onClick={this.toggleNav}
                    className="nav-link ml-3 mr-3 text-dark"
                    to="/about"
                  >
                    <span className="fa fa-file-text fa-lg"></span> Gii thiu
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink
                    className="nav-link ml-3 mr-3 text-dark"
                    to="/contact"
                    onClick={this.toggleNav}
                  >
                    <span className="fa fa-volume-control-phone fa-lg"></span>
                    Lin H
                  </NavLink>
                </NavItem>
              </Nav>
            </Collapse>
          </div>
        </Navbar>

        <Jumbotron className="bannerimg">
          <div className="container">
            <div className="row row-header">
              <div className="col-12 col-sm-6 text-bannerimg">
                <h1>Ristorante Con Fusion</h1>
                <i>
                  <p>
                    Cuc i nh mt tch tr......
                    <br />
                    ..... tt nhin s khng ng c i,
                    <br /> Nhng phi tri qua d v  mi cm nhn c v
                    ngt.
                  </p>
                </i>
              </div>
            </div>
          </div>
        </Jumbotron>
        <marquee>
          <b className="text-danger">THNG BO:</b> Bt u t ngy 15/9/2021
          Ca hng <b className="text-danger">tr li hot ng bnh thng</b>{" "}
          v vy rt mong nhn c s ng h t cc tn  tr sa.{"   "}
          <b className="text-danger">
            <i>
              Ca hng vn tip tc FREE SHIP cho cc n hng t 99k trong phm
              vi 15KM. Xin cm n!
            </i>
          </b>
        </marquee>
      </React.Fragment>
    );
  }
}
export default Header;



