import React from "react";
import {
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
  CarouselCaption,
} from "reactstrap";

//item slideshow
const items = [
  {
    src: "/images/a.jpg",
    altText: "Slide 1",
    caption: "Hy nh!",
    captions: "Tr sa phi c trn chu..... Bn b thm cht v nhau mi bn",
  },
  {
    src: "/images/b.jpg",
    altText: "Slide 2",
    caption: "C nhng lc",
    captions: "Anh i mt tr sa v du, khng chn tru v nhiu thch.",
  },
  {
    src: "/images/c.jpg",
    altText: "Slide 3",
    caption: "i khi!",
    captions:
      "...1 ngy c 24h, Trong vng 1h phi c 1 ly tr sa, 23h cn li suy ngh xem ngy mai ung v g",
  },
];

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = { activeIndex: 0 };
    this.next = this.next.bind(this);
    this.previous = this.previous.bind(this);
    this.goToIndex = this.goToIndex.bind(this);
    this.onExiting = this.onExiting.bind(this);
    this.onExited = this.onExited.bind(this);
  }

  onExiting() {
    this.animating = true;
  }

  onExited() {
    this.animating = false;
  }

  next() {
    if (this.animating) return;
    const nextIndex =
      this.state.activeIndex === items.length - 1
        ? 0
        : this.state.activeIndex + 1;
    this.setState({ activeIndex: nextIndex });
  }

  previous() {
    if (this.animating) return;
    const nextIndex =
      this.state.activeIndex === 0
        ? items.length - 1
        : this.state.activeIndex - 1;
    this.setState({ activeIndex: nextIndex });
  }

  goToIndex(newIndex) {
    if (this.animating) return;
    this.setState({ activeIndex: newIndex });
  }

  render() {
    const { activeIndex } = this.state;

    const slides = items.map((item) => {
      return (
        <CarouselItem
          className="carouselImg"
          onExiting={this.onExiting}
          onExited={this.onExited}
          key={item.src}
        >
          <img src={item.src} alt={item.altText} />
          <CarouselCaption
            captionText={item.captions}
            captionHeader={item.caption}
          />
        </CarouselItem>
      );
    });

    return (
      <div className="row">
        <div className="col-12">
          <div className="row">
            <div className="col-12 col-md-4 mb-5 ml-3">
              <img src="/assets/images/sttTrasua2.jpg" width="300px" alt="" />
            </div>
            <div className="col-12 col-md-7">
              <p style={{ fontSize: "25px" }}>
                <i>
                  Tr sa thc ung pha trn gia tr v sa kt hp thm cc
                  loi trn chu, thch to cm gic mt lnh, sng khoi. Ngi
                  bnh thng 60% c th ngi l nc, ring vi tn  tr sa
                  c l 80% trng lng c th dng  cha tr sa.
                </i>
              </p>
            </div>
          </div>
        </div>
        <Carousel
          className="col-12 slides"
          activeIndex={activeIndex}
          next={this.next}
          previous={this.previous}
        >
          <CarouselIndicators
            items={items}
            activeIndex={activeIndex}
            onClickHandler={this.goToIndex}
          />
          {slides}
          <CarouselControl
            direction="prev"
            directionText="Previous"
            onClickHandler={this.previous}
          />
          <CarouselControl
            direction="next"
            directionText="Next"
            onClickHandler={this.next}
          />
        </Carousel>
        <div className="col-12">
          <div className="row  mt-5 mb-5">
            <div className="col-12 col-md-7 ml-3">
              <p style={{ fontSize: "25px" }}>
                <i>
                  Thanh xun cha gu cng nh ung tr sa khng c trn chu.
                  C ngi cn c ngi khng nhng i khi ung tr sa khng
                  trn tru vn ngon nh thng.
                </i>
              </p>
            </div>
            <div className="col-12 col-md-4">
              <img src="/assets/images/sttTrasua1.jpg" width="350px" alt="" />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Home;



