import React, { Component } from "react";
import {
  Breadcrumb,
  BreadcrumbItem,
  Button,
  Label,
  Row,
  Col,
  Modal,
  ModalHeader,
  ModalBody,
} from "reactstrap";
import { Link } from "react-router-dom";
import { Control, LocalForm } from "react-redux-form";

//to cc validate cho form

class Contact extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isModalOpen: false,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.toggleModal = this.toggleModal.bind(this);
  }

  toggleModal() {
    this.setState({ isModalOpen: !this.state.isModalOpen });
  }

  handleSubmit(value) {
    alert("Cm n bn " + value.fullname + "  gi phn hi");
    this.setState({ isModalOpen: !this.state.isModalOpen });
  }

  render() {
    return (
      <div className="container">
        <div className="row">
          <Breadcrumb>
            <BreadcrumbItem>
              <Link to="/home">Home</Link>
            </BreadcrumbItem>
            <BreadcrumbItem active>Lin h</BreadcrumbItem>
          </Breadcrumb>
          <div className="col-12">
            <h3>Lin h vi chng ti</h3>
            <hr />
          </div>
        </div>
        <div className="row row-content">
          <div className="col-12">
            <h3>Thng tin lin h</h3>
            <br />
          </div>
          <div className="col-12 col-sm-3 offset-sm-1">
            <h5>a ch ca hng:</h5>
            <address className="text-dark" style={{ fontSize: "20px" }}>
              214, Long Hng
              <br />
              TT Hng H, Hng H
              <br />
              Thi Bnh
              <br />
              <p>Lin h t hng</p>
              <i className="fa fa-facebook"></i>:
              <a href="https://www.facebook.com/bichdao.vu.3">Mr o</a>
              <br />
              <i className="fa fa-phone fa-lg"></i>: +84397580689
              <br />
              <i className="fa fa-envelope fa-lg"></i>:
              <a href="mailto:buiduykhanh1901@gmail.com">Tr sa Chill..</a>
            </address>
          </div>
          <div className="col-12 col-sm-7 offset-sm-1 text-center">
            <h5>V tr trn map</h5>
            <img src="images/map.png" width="600px" alt="map" />
          </div>
          <div className="col-12 col-sm-11 offset-sm-1">
            <div className="btn-group" role="group">
              <a
                role="button"
                className="btn btn-primary"
                href="tel:+84397580689"
              >
                <i className="fa fa-phone"></i> Call
              </a>
              <a role="button" className="btn btn-info"
                target="_blank"
                rel="noopener noreferrer"
                href="https://www.facebook.com/Chill-MilkTea-FastFood-214-%C4%90%C6%B0%E1%BB%9Dng-Long-H%C6%B0ng-TT-H%C6%B0ng-H%C3%A0-103010185416959">
                <i className="fa fa-facebook"></i> Facebook
              </a>
              <a
                role="button"
                className="btn btn-success"
                href="mailto:buiduykhanh1901@gmail.com"
              >
                <i className="fa fa-envelope-o"></i> Email
              </a>
            </div>
            <hr />
          </div>
          <div>
            <h3>Phn hi ca bn v chng ti chng ti</h3>
            <p>
              Bn c  kin vng mc v cht lng sn phm, cht lng phc
              v, gi c hoc gp  cho nhng iu  vui lng in y  thng
              tin vo Form di y
            </p>
            <Button onClick={this.toggleModal} >
              Form Feedback <span className="fa fa-comments-o"></span>
            </Button>
          </div>
        </div>
        <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
          <ModalHeader toggle={this.toggleModal}>
            Gi phn hi ca bn
          </ModalHeader>
          <ModalBody>
            <div className="row">
              <div className="col-12">
                <div className="col-12">
                  <LocalForm onSubmit={(value) => this.handleSubmit(value)}>
                    <Row className="form-group">
                      <Label htmlFor="fullname" md={3}>
                        Tn bn
                      </Label>
                      <Col md={9}>
                        <Control.text
                          model=".fullname"
                          id="fullname"
                          name="fullname"
                          placeholder="Nhp tn ca bn"
                          required
                        />
                      </Col>
                    </Row>
                    <Row className="form-group">
                      <Label htmlFor="fb" md={3}>
                        a ch Facebook
                      </Label>
                      <Col md={9}>
                        <Control.text
                          model=".fb"
                          id="fb"
                          name="fb"
                          placeholder="Link Facebook c nhn"
                          required
                        />
                      </Col>
                    </Row>
                    <Row className="form-group">
                      <Label htmlFor="depart" md={3}>Danh mc SP: </Label>
                      <Col md={9}>
                        <Control.select
                          model="depart"
                          id="depart"
                          name="depart"
                        >
                          <option> ung</option>
                          <option> n nhanh</option>
                        </Control.select>
                      </Col>
                    </Row>
                    <Row className="form-group">
                      <Label htmlFor="proname" md={3}>
                        Tn Sn phm:
                      </Label>
                      <Col md={9}>
                        <Control.text
                          model=".proname"
                          id="proname"
                          name="proname"
                          placeholder="V d: Tr sa TC ng en"
                          required
                        />
                      </Col>
                    </Row>
                    <Row className="form-group">
                      <Label htmlFor="score" md={3}>
                        nh gi:
                      </Label>
                      <Col md={9}>
                        <Control.select
                          model=".score"
                          id="score"
                          name="score"
                          >
                            <option>5.0</option>
                            <option>6.0</option>
                            <option>7.0</option>
                            <option>8.0</option>
                            <option>9.0</option>
                            <option>10.0</option>
                          </Control.select>
                      </Col>
                    </Row>
                    <Row className="form-group">
                      <Label htmlFor="feedback" md={3}>
                        Phn hi:{" "}
                      </Label>
                      <Col md={9}>
                        <Control.textarea
                          model=".feedback"
                          id="feedback"
                          name="feedback"
                        />
                      </Col>
                    </Row>
                    <Button type="submit" color="primary">
                      Gi
                    </Button>
                  </LocalForm>
                </div>
              </div>
            </div>
          </ModalBody>
        </Modal>
      </div>
    );
  }
}
export default Contact;



