import React from "react";
import { Breadcrumb, BreadcrumbItem, Card, CardImg } from "reactstrap";
import { Link } from "react-router-dom";
import { Fade } from "react-animation-components";

function Department(props) {
  const departments = props.department.map((department) => {
    return (
      <Fade in className="col-12 col-md-6 col-lg-4" key={department.id}>
        <Card className="m-3 text-center bg-info text-light border-light">
          <Link
            style={{ textDecoration: "none"}}
            to="/detailmenu"
          >
            <h4 className="mt-2 text-light">{department.name}</h4>
            <p className="text-warning">S lng mn: {department.numberOfProduct}</p>
            <CardImg src={department.img} />
          </Link>
        </Card>
      </Fade>
    );
  });

  return (
    <div className="container">
      <div className="row mt-4">
        <Breadcrumb>
          <BreadcrumbItem>
            <Link to="/home">Home</Link>
          </BreadcrumbItem>
          <BreadcrumbItem active>Danh mc sn phm</BreadcrumbItem>
        </Breadcrumb>
      </div>
      <div className="text-dark">
        <h3>Danh mc chnh</h3>
        <br />
      </div>
      <div className="row mb-5">{departments}</div>
    </div>
  );
}

export default Department;



