import React, { Component } from "react";
import {
  Card,
  CardImg,
  CardTitle,
  Breadcrumb,
  BreadcrumbItem,
  Button,
  Form,
  FormGroup,
  Input,
} from "reactstrap";
import { Link } from "react-router-dom";
import { FadeTransform } from "react-animation-components";

class Menu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      products: this.props.products,
      name: "",
      isModalOpen: false,
    };
    this.searchName = this.searchName.bind(this);
    this.handleInputChanged = this.handleInputChanged.bind(this);
  }

  // phng thc lc input value trong staffs nhn value c ch hoa v ch thng
  searchName(event) {
    const result = this.props.products.filter((s) =>
      s.name.toLowerCase().match(this.state.name)
    );
    this.setState({
      products: result,
      name: this.name.value,
    });
    event.preventDefault();
  }

  //khi input c value ngay lp tc s truyn vo name
  //s dng cho nt tm kim
  handleInputChanged(event) {
    this.setState({
      name: event.target.value,
    });
  }

  render() {
    const RenderMenuItem = ({ product }) => {
      return (
        <FadeTransform
          in
          transformProps={{
            exitTransform: "scale(0.5) translateY(-50%)",
          }}
        >
          <Card>
            <Link
              style={{ textDecoration: "none", hoverColor: "black" }}
              to={`/menu/${product.id}`}
            >
              <CardImg width="100%" src={product.image} alt={product.name} />
              <CardTitle className="text-left text-danger mt-3">
                {product.name}
              </CardTitle>
            </Link>
            <a
              href="https://www.facebook.com/bichdao.vu.3"
              target="_blank"
              rel="noopener noreferrer"
            >
              <Button
                type="button"
                className="btn btn-warning fa fa-shopping-cart"
              >
                t ngay
              </Button>
            </a>
          </Card>
        </FadeTransform>
      );
    };

    const menu = this.state.products.map((product) => {
      return (
        <div className="col-10 col-md-6 col-lg-6 mb-5" key={product.id}>
          <RenderMenuItem product={product} />
        </div>
      );
    });

    return (
      <div className="container">
        <div className="row">
          <Breadcrumb>
            <BreadcrumbItem>
              <Link to="/home">Home</Link>
            </BreadcrumbItem>
            <BreadcrumbItem active> Ni bt</BreadcrumbItem>
          </Breadcrumb>
          <div className="col-12">
            <h3>Bn thch n g....</h3>
            <hr />
          </div>
        </div>
        <Form onSubmit={this.searchName} className="row">
          <FormGroup md={6}>
            <Input
              className="form-control mb-3"
              type="text"
              id="search"
              name="search"
              placeholder="Bn mun tm g ?"
              onChange={this.handleInputChanged}
              innerRef={(input) => (this.name = input)}
            />
          </FormGroup>
          <Button
            md={3}
            type="submit"
            value="submit"
            color="primary"
            className="mb-5 ml-3"
          >
            Tm
          </Button>
        </Form>
        <div className="row">{menu}</div>
        <div className="row">
          <Link to='/detailmenu'>
            <Button
              type="button"
              className="btn btn-info fa fa-list"
            > Xem thc n chi tit...</Button>
          </Link>
        </div>
      </div>
    );
  }
}

export default Menu;



